import React, { useEffect, useRef, useState, useMemo } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Modal from "@mui/material/Modal";
import { EventAPI } from "../../../commons/EventAPI";
import PubSub from 'pubsub-js';

const dispatchedEvents = [EventAPI.MODAL_CLOSED];

const SrsMuiModal = function (props, ref) {
  const [open, setOpen] = useState(props.open || true);
  const innerRef = useRef(null);

  const closeModal = (eventName, eventData) => {
    setOpen(false);
    let sourceId = eventData?.detail?.payload?.id || "";
    props.dispatchEvent(EventAPI.MODAL_CLOSED, {id: sourceId, modalId: props.elementid})
  }

  useEffect(() => {
    let token = PubSub.subscribe("MODAL_CLOSED", closeModal);

    return () => {
      PubSub.unsubscribe(token);
    };
  }, []);

  useEffect(() => {
    setOpen(props.open);
  }, [props.open]);

  return (
    <div ref={innerRef}>
      <Modal
        key={props.elementid}
        open={open}
        disableEnforceFocus={props.disableEnforceFocus}
        disableAutoFocus={props.disableAutoFocus}
        disableBackdropClick={props.disableBackdropClick}
        disableEscapeKeyDown={props.disableEscapeKeyDown}
        sx={props.sx}
        theme={props.muiTheme}>
        <div>
          <slot name="close_modal"/>
          <slot />
        </div>
      </Modal>
    </div>
  );
};

const styles = `
  /* Custom styles for the MuiModal component */
`;

const props = [
  {
    name: "open",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disableEnforceFocus",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disableAutoFocus",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disableBackdropClick",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disableEscapeKeyDown",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiModal),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
